import { Container, Grid, makeStyles, Typography } from "@material-ui/core";
import React from "react";
import gavel from "../../assets/images/statistic/gavel.webp";
import analytics from "../../assets/images/statistic/analytics.webp";
import layers from "../../assets/images/statistic/layers.webp";
import briefcase from "../../assets/images/statistic/briefcase.webp";

const Statictic = ({ t, statistics }) => {
  const classes = useStyles();
  const statisticArray = [
    { img: gavel, count: statistics?.total_transactions, title: t("Static1") },
    { img: layers, count: statistics?.total_users, title: t("Static2") },
    { img: analytics, count: statistics?.turnover_perday, title: t("Static3") },
    { img: briefcase, count: statistics?.general_reserve, title: t("Static4") },
  ];
  return (
    <section className="statistic dark" style={{ padding: "64px 0" }}>
      <Container maxWidth="lg">
        <Typography variant="h2" style={{ marginBottom: 48 }}>
          {t("StaticTitle")}
        </Typography>
        <Grid container justifyContent="center" spacing={2}>
          {statisticArray.map((item, idx) => (
            <Grid item xs={6} sm={6} md={3} lg={3}>
              <Grid
                container
                alignItems="center"
                direction="column"
                className={classes.gridItem}
              >
                <img src={item.img} alt="" />
                <Typography
                  variant="h2"
                  className={classes.title}
                  style={{
                    textAlign: "center",
                    fontWeight: "500",
                    fontSize: "34px",
                    color: "#FCE34D",
                  }}
                >
                  {item.count}
                </Typography>
                <Typography
                  className={classes.subtitle}
                  style={{
                    textAlign: "center",
                    fontWeight: "300",
                    lineHeight: "1",
                  }}
                  component="span"
                >
                  {item.title}
                </Typography>
              </Grid>
            </Grid>
          ))}
        </Grid>
      </Container>
    </section>
  );
};

export default Statictic;

const useStyles = makeStyles((theme) => ({
  title: {
    [theme.breakpoints.down(1200)]: {
      fontSize: 40,
      lineHeight: "1",
      textAlign: "center",
    },

    [theme.breakpoints.down(576)]: {
      fontSize: 23,
    },
  },

  subtitle: {
    fontSize: 23,
    [theme.breakpoints.down(576)]: {
      fontSize: 16,
    },
  },

  gridItem: {
    background: "#2B2B2B",
    height: 368,
    borderRadius: 8,
    transition: "all 0.2s linear",
    padding: "30px",
    flexWrap: "nowrap",

    "& img": {
      width: 120,
      height: 120,
      margin: "16px 0 32px",
      transition: "all 0.2s linear",

      [theme.breakpoints.down(768)]: {
        margin: 0,
      },
    },

    "&:hover": {
      boxShadow: "0 0 10px #FCE34D",

      "& img": {
        transform: "scale(1.1)",
      },
    },
    [theme.breakpoints.down(576)]: {
      height: 182,
      padding: "10px",

      "& img": {
        height: "72px",
        width: "72px",
        marginBottom: 8,
      },
    },
  },
}));
