import React, { useEffect, useState } from "react";
import {
  Button,
  Container,
  Grid,
  InputLabel,
  makeStyles,
  Select,
  Typography,
  useTheme,
  useMediaQuery,
  MenuItem,
  Box,
  FormControl,
  TextField,
} from "@material-ui/core";
import success from "../../assets/images/state/saccess-lg.webp";
import recycle from "../../assets/images/state/recycle.webp";
import dollar from "../../assets/images/state/dollarCoin.webp";
import visa from "../../assets/images/paymentsSystem/visa.svg";
import masterCard from "../../assets/images/paymentsSystem/masterCard.svg";
import { MediumButton } from "../../shared/Buttons";
import { useHistory } from "react-router";
import { useSelector } from "react-redux";
import CompareArrowsIcon from "@mui/icons-material/CompareArrows";
import ReviewsCount from "./ReviewsCount";

const BuyCrypto = ({ t }) => {
  const history = useHistory();
  const theme = useTheme();
  const tablet = useMediaQuery(theme.breakpoints.down(992));
  const mobile = useMediaQuery(theme.breakpoints.down(576));

  const { СurrenciesList } = useSelector(
    ({ СurrenciesReducer }) => СurrenciesReducer
  );

  const [selectCurrencies, setSelectCurrencies] = useState({
    currency: 1000.0,
    condition: "",
    conditionId: 2,
  });

  useEffect(() => {
    setSelectCurrencies((selectCurrencies) => ({
      ...selectCurrencies,
      condition: СurrenciesList.find((item) => item.id === "2"),
    }));
    // eslint-disable-next-line
  }, [СurrenciesList]);

  const handleValueChange = ({ target: { value, name } }) => {
    setSelectCurrencies((selectCurrencies) => ({
      ...selectCurrencies,
      [name]: value,
    }));
  };
  const total = selectCurrencies?.currency / +selectCurrencies?.condition?.rate;
  const comm = (total * +selectCurrencies?.condition?.commission) / 100;
  const sum = total - comm;
  const useStyles = makeStyles((theme) => ({
    title: {
      fontSize: 48,
      fontWeight: 500,
      color: "#000",
      marginBottom: 24,
      [theme.breakpoints.down(992)]: {
        textAlign: "center",
      },

      [theme.breakpoints.down(576)]: {
        fontSize: 39,
      },
    },
    subtitle: {
      color: "#323A46",
      lineHeight: "22px",
      fontWeight: 400,
      maxWidth: 614,
      borderBottom: "1px solid #E7EAEE",
      paddingBottom: 32,
      marginBottom: 32,

      [theme.breakpoints.down(992)]: {
        textAlign: "center",
        maxWidth: "none",
      },
    },

    buttonBlack: {
      marginTop: 24,
      [theme.breakpoints.down(992)]: {
        height: 56,
        width: "100%",
        marginTop: 16,
      },
    },

    grid: {
      borderBottom: "1px solid #E7EAEE",
      paddingBottom: 32,
      marginBottom: 32,
    },

    button: {
      height: 56,
      width: "100%",
      display: "block",
      marginTop: 16,
    },

    formControlFirst: {
      boxShadow: "none",
      "& .MuiInputLabel-formControl": {
        left: 14,
      },

      "& .MuiInputLabel-shrink": {
        left: 0,
      },
    },

    formControl: {
      marginBottom: 16,
      boxShadow: "none",

      "& .MuiOutlinedInput-notchedOutline": {
        borderColor: "#B8C0CC",
      },

      "& .MuiSelect-root": {
        display: "flex",
        alignItems: "center",
      },
    },
    img: {
      width: 30,
      height: 30,
      marginRight: 10,
    },

    calculator: {
      marginTop: 20,
    },
    icon: {
      [theme.breakpoints.down(768)]: {
        transform: "rotate(90deg)",
      },
    },
  }));
  const classes = useStyles();
  return (
    <section className="buy-crypto light">
      <Container maxWidth="lg">
        <Grid container justifyContent={tablet ? "center" : "space-between"}>
          <Grid item xs={12} sm={12} md={6} lg={6} xl={6}>
            <Typography className={classes.title}>
              {t("BuyCurrencies")}
            </Typography>
            <Typography variant="h6" className={classes.subtitle}>
              {t("BuyDescription")}
            </Typography>
            <Grid container alignItems="center" className={classes.grid}>
              <Grid
                container
                alignItems="center"
                justifyContent={tablet ? "center" : ""}
              >
                <ReviewsCount buy t={t} />
              </Grid>
              <MediumButton
                variant="contained"
                className={classes.buttonBlack}
                color="secondary"
                onClick={() => history.push("/main/reviews")}
                size="medium"
              >
                {t("ReadReviews")}
              </MediumButton>
            </Grid>
            <div className="roadmap">
              <div className="roadmap-img">
                <img src={dollar} alt="" />
              </div>
              <div className="roadmap-img">
                <img src={recycle} alt="" />
              </div>
              <div className="roadmap-img">
                <img src={success} alt="" />
              </div>
            </div>
          </Grid>
          <Grid
            item
            xs={12}
            sm={12}
            md={6}
            lg={6}
            xl={6}
            container
            justifyContent="flex-end"
          >
            <form>
              <Typography
                variant="body1"
                style={{ color: "#000", marginBottom: 8 }}
              >
                {t("Currency")}
              </Typography>
              <FormControl
                className={`${classes.formControl} ${classes.formControlFirst}`}
              >
                <InputLabel>{t("SelectCurrency")}</InputLabel>
                <Select
                  fullWidth
                  name="conditionId"
                  label={t("SelectCurrency")}
                  variant="outlined"
                  value={selectCurrencies.conditionId}
                  onChange={handleValueChange}
                >
                  {Array.isArray(СurrenciesList) &&
                    СurrenciesList.map((item, index) => {
                      return (
                        <MenuItem
                          key={index}
                          value={item.id}
                          onClick={() =>
                            setSelectCurrencies((selectCurrencies) => ({
                              ...selectCurrencies,
                              condition: item,
                            }))
                          }
                        >
                          <img className={classes.img} src={item.logo} alt="" />
                          {item.name}
                        </MenuItem>
                      );
                    })}
                </Select>
              </FormControl>
              <Typography
                variant="body1"
                style={{ color: "#000", marginBottom: 8 }}
              >
                {t("USDT")}
              </Typography>
              <TextField
                type="text"
                label={t("EnterAmount")}
                name="currency"
                className={classes.formControl}
                value={selectCurrencies.currency}
                onChange={handleValueChange}
                variant="outlined"
                onKeyPress={(event) => {
                  if (!/[0-9.,]/.test(event.key)) {
                    event.preventDefault();
                  }
                }}
              />

              {selectCurrencies.currency && (
                <Box className={classes.calculator}>
                  <Typography variant="h6" style={{ color: "#777" }}>
                    {t("inTotal")}
                  </Typography>
                  <Box
                    display="flex"
                    alignItems="center"
                    justifyContent="space-between"
                    sx={{ flexDirection: { xs: "column", sm: "row" } }}
                  >
                    <Box
                      display="flex"
                      alignItems="center"
                      style={{ color: "#000" }}
                      sx={{ flexDirection: { xs: "column", sm: "row" } }}
                    >
                      <Typography variant="body2">
                        {t("atRate")} 1.00{" "}
                        {selectCurrencies?.condition?.currency}
                      </Typography>
                      <Box style={{ padding: "0 10px" }}>
                        {" "}
                        <CompareArrowsIcon className={classes.icon} />
                      </Box>
                      <Typography
                        variant="body2"
                        style={{ fontSize: mobile ? 14 : 16 }}
                      >
                        {selectCurrencies?.condition?.rate}
                      </Typography>
                    </Box>
                    <Box
                      display="flex"
                      alignItems="center"
                      style={{ color: "#000", fontSize: mobile ? 14 : 16 }}
                    >
                      ~ {sum.toFixed(selectCurrencies?.condition?.decimal)}{" "}
                      {selectCurrencies?.condition?.currency}
                    </Box>
                  </Box>
                </Box>
              )}

              <Button
                variant="contained"
                className={classes.button}
                color="primary"
                onClick={() =>
                  history.push(
                    `/dashboard/buy/details/${selectCurrencies.condition.currency}`
                  )
                }
              >
                {t("buy")}
              </Button>
              {/* <Button
                variant="contained"
                className={classes.button}
                color="secondary"
                style={{ display: "flex", alignItems: "center" }}
                href="https://netex-kassa.com/"
                target="_blank"
              >
                <img
                  src={netex}
                  style={{ height: 32, marginRight: 16 }}
                  alt=""
                />
                Netex Kassa
              </Button> */}
              <Button
                variant="outlined"
                className={classes.button}
                color="secondary"
                onClick={() => history.push("/dashboard/replenish/fiat")}
              >
                {t("BuyBankCard")}
              </Button>
              <Grid
                container
                justifyContent="center"
                direction="column"
                alignItems="center"
                style={{
                  borderTop: "1px solid #E7EAEE",
                  paddingTop: 24,
                  marginTop: 24,
                }}
              >
                <Typography style={{ color: "#000" }}>
                  {t("WeAccept")}
                </Typography>
                <Grid container alignItems="center" justifyContent="center">
                  <img src={masterCard} style={{ marginRight: 16 }} alt="" />
                  <img src={visa} alt="" />
                </Grid>
              </Grid>
            </form>
          </Grid>
        </Grid>
      </Container>
    </section>
  );
};

export default BuyCrypto;
