import { Button, Container, makeStyles, Typography } from "@material-ui/core";
import React from "react";
import Card from "../../components/NewsCard";
import { useHistory } from "react-router-dom";

const News = ({ t, i18n, news }) => {
  const classes = useStyles();
  const history = useHistory();
  const NewsContent = Object.values(typeof news === "object" && news);

  return (
    <section className="news light">
      <Container maxWidth="lg">
        <Typography variant="h2" style={{ color: "#000", marginBottom: 30 }}>
          {t("NewsTitle")}
        </Typography>
        {NewsContent?.slice(0, 4).map((item, idx) => (
          <Card item={item} key={idx} i18n={i18n} />
        ))}
        <Button
          variant="contained"
          size="large"
          className={classes.button}
          color="secondary"
          style={{ display: "table", margin: "48px auto 0" }}
          onClick={() => history.push("/main/news")}
        >
          {t("ReadNews")}
        </Button>
      </Container>
    </section>
  );
};

export default News;

const useStyles = makeStyles((theme) => ({
  button: {
    [theme.breakpoints.down(992)]: {
      height: 56,
      width: "100%",
    },
  },
}));
