import React from "react";
import clock from "../../assets/images/benefits/alarm.webp";
import startup from "../../assets/images/benefits/startup.webp";
import cup from "../../assets/images/benefits/cup.webp";
import shield from "../../assets/images/benefits/shield.webp";
import chatting from "../../assets/images/benefits/chatting.webp";
import { Container, Grid, makeStyles, Typography } from "@material-ui/core";

const Benefits = ({ t }) => {
  const classes = useStyles();
  const benefitsArray = [
    { title: t("benefits1"), img: cup },
    { title: t("benefits2"), img: shield },
    { title: t("benefits3"), img: clock },
    { title: t("benefits4"), img: startup },
    { title: t("benefits5"), img: chatting },
  ];
  return (
    <section className="benefits dark">
      <Container maxWidth="lg">
        <Grid container justifyContent="center" alignItems="center" spacing={2}>
          {benefitsArray.map((item, idx) => (
            <Grid item xs={6} sm={6} md={3} lg={3} xl={3}>
              <Grid
                container
                direction="column"
                justifyContent="center"
                className={classes.gridItem}
                alignItems="center"
              >
                <img src={item.img} alt="" />
                <Typography
                  variant="h5"
                  style={{
                    textAlign: "center",
                    marginTop: 16,
                    fontWeight: "300"
                  }}
                >
                  {item.title}
                </Typography>
              </Grid>
            </Grid>
          ))}
        </Grid>
      </Container>
    </section>
  );
};

export default Benefits;

const useStyles = makeStyles((theme) => ({
  gridItem: {
    height: 182,
    background: "#2B2B2B",
    borderRadius: 8,
    transition: "all 0.2s linear",
    padding: "0 19px",

    "& img": {
      height: 72,
      transition: "all 0.2s linear",
    },

    "&:hover": {
      boxShadow: "0 0 10px #FCE34D",

      "& img": {
        transform: "scale(1.1)",
      },
    },
  },
}));
