import {
  Container,
  Card,
  Grid,
  Typography,
  Button,
  makeStyles,
  useMediaQuery,
  useTheme,
} from "@material-ui/core";
import React from "react";
import { Box } from "@mui/system";
import Diagram from "../../components/chart";
import { useHistory } from "react-router-dom";

const Courses = ({ t, currencies }) => {
  const classes = useStyles();
  const theme = useTheme();
  const tablet = useMediaQuery(theme.breakpoints.down(992));

  const history = useHistory();

  const currenciesContent = Object.values(
    typeof currencies === "object" && currencies
  );
  return (
    <section className="courses light">
      <Container>
        <Grid
          className={classes.courseTableHead}
          container
          direction="row"
          justifyContent="space-between"
          alignItems="center"
        >
          <Grid item md={3}>
            <Typography className={classes.courseTableHeadCell}>
              {t("coinTitle")}
            </Typography>
          </Grid>
          <Grid item md={3}>
            <Typography className={classes.courseTableHeadCell}>
              {t("price")}
            </Typography>
          </Grid>
          {!tablet && (
            <>
              <Grid item md={3}>
                <Typography className={classes.courseTableHeadCell}>
                  {t("dynamic")}
                </Typography>
              </Grid>
              <Grid item md={3}>
                <Typography className={classes.courseTableHeadCell}>
                  {t("market")}
                </Typography>
              </Grid>
            </>
          )}
        </Grid>

        <Box>
          {Array.isArray(currenciesContent) &&
            currenciesContent
              .filter((item) => item.currency !== "KOKO")
              .map((coin, index) => {
                return (
                  <Card className={classes.courseTableRow} key={index}>
                    <Grid
                      className={classes.courseTableRowContanier}
                      container
                      direction="row"
                      justifyContent="space-between"
                      alignItems="center"
                    >
                      <Grid
                        className={classes.courseTableCoinCell}
                        item
                        md={3}
                        container
                        direction="row"
                        justifyContent="center"
                        alignItems="center"
                      >
                        <Box sx={{ display: "flex", alignItems: "center" }}>
                          <Box className={classes.courseCoinImgWrapper}>
                            <img
                              style={{ objectFit: "contain" }}
                              width="40"
                              height="40"
                              src={coin.logo}
                              alt="Coin icon"
                            />
                          </Box>
                          <Box className={classes.courseTableCoinNames}>
                            <Box mr={1}>
                              <Typography
                                className={classes.courseTableBoldText}
                              >
                                {coin.currency}
                              </Typography>
                            </Box>
                            <Typography className={classes.courseCoinFullName}>
                              {coin.name}
                            </Typography>
                          </Box>
                        </Box>
                      </Grid>

                      <Grid
                        className={classes.courseTableCoinPrice}
                        item
                        md={3}
                        container
                        direction="row"
                        justifyContent="center"
                        alignItems="center"
                      >
                        <Typography className={classes.courseTableBoldText}>
                          ${coin.rate}
                        </Typography>
                      </Grid>

                      {!tablet && (
                        <>
                          <Grid
                            item
                            md={3}
                            container
                            direction="row"
                            justifyContent="center"
                            alignItems="center"
                          >
                            <Box
                              className={classes.courseCoinDynamic}
                              style={{
                                background:
                                  coin.difference[0] === "+"
                                    ? "#ECFDF5"
                                    : coin.difference[0] === "-"
                                    ? "#FEF2F2"
                                    : "#F7F8F9",
                                color:
                                  coin.difference[0] === "+"
                                    ? "#10B981"
                                    : coin.difference[0] === "-"
                                    ? "#EF4444"
                                    : "#A0ABBB",
                              }}
                            >
                              <Typography
                                color="inherit"
                                className={classes.courseTableBoldText}
                              >
                                {coin.difference}
                              </Typography>
                            </Box>
                          </Grid>
                          <Grid
                            item
                            md={3}
                            container
                            direction="row"
                            justifyContent="center"
                            alignItems="center"
                          >
                            <Box style={{ width: 100 }}>
                              <Diagram
                                height={37}
                                color={
                                  coin.difference[0] === "+"
                                    ? "#10B981"
                                    : coin.difference[0] === "-"
                                    ? "#F15A5A"
                                    : "#C1C7D2"
                                }
                                rates={coin.rates}
                              />
                            </Box>
                          </Grid>
                        </>
                      )}
                    </Grid>
                  </Card>
                );
              })}
        </Box>
        <Button
          className={classes.courseButton}
          variant="contained"
          color="secondary"
          size="large"
          onClick={() => history.push("/dashboard/rates")}
        >
          {t("ViewAllCourses")}
        </Button>
      </Container>
    </section>
  );
};

export default Courses;

const useStyles = makeStyles((theme) => ({
  root: {},
  courseTableHead: {
    padding: "0 0 24px",
    background: "transparent",
    [theme.breakpoints.down(992)]: {
      padding: "0 40px 24px",
    },
    [theme.breakpoints.down(576)]: {
      padding: "0 14px 24px",
    },
  },
  courseTableHeadCell: {
    fontSize: "20px",
    lineHeight: "24px",
    color: "#64748B",
    border: "none",
    textAlign: "center",
  },

  courseTableRow: {
    boxShadow: "0px 1px 0px #E2E8F0",
    borderRadius: "16px",
    padding: "16px 0 16px 0",
    marginBottom: "8px",
    [theme.breakpoints.down(992)]: {
      padding: "16px 40px 16px 40px",
    },
    [theme.breakpoints.down(576)]: {
      padding: "16px 15px 16px 15px",
    },
  },

  courseTableBoldText: {
    color: "inherit",
    fontWeight: 600,
    fontSize: "19px",
    lineHeight: "23px",
    textAlign: "center",
    [theme.breakpoints.down(992)]: {
      textAlign: "left",
      fontSize: "28px",
    },
    [theme.breakpoints.down(576)]: {
      fontSize: "23px",
    },
    [theme.breakpoints.down(320)]: {
      fontSize: "20px",
    },
  },

  courseCoinDynamic: {
    fontWeight: 600,
    fontSize: "19px",
    lineHeight: "23px",
    textAlign: "center",
    background: "#ECFDF5",
    padding: "8px 12px",
    borderRadius: "8px",
    color: "#10B981",
  },
  courseCoinImgWrapper: {
    marginRight: "16px",
    [theme.breakpoints.down(992)]: {
      marginRight: "20px",
    },
    [theme.breakpoints.down(576)]: {
      marginRight: "8px",
    },
  },
  courseTableBodyRow: {
    background: "#fff",
    padding: "16px 32px",
    boxShadow: "0px 1px 0px #E2E8F0",
    borderRadius: "16px",
    marginBottom: "16px",
  },
  courseTableCoinCell: {
    justifyContent: "flex-start",
    paddingLeft: "60px",
    [theme.breakpoints.down(992)]: {
      paddingLeft: "0",
    },
  },
  courseButton: {
    display: "block",
    margin: "24px auto 0 auto",
  },
  courseTableCoinNames: {
    display: "flex",
    [theme.breakpoints.down(992)]: {
      flexDirection: "column",
    },
  },
  courseTableRowContanier: {
    [theme.breakpoints.down(992)]: {
      flexWrap: "nowrap",
    },
  },
  courseTableCoinPrice: {
    [theme.breakpoints.down(992)]: {
      justifyContent: "flex-end",
    },
  },
  courseCoinFullName: {
    color: "#64748B",
    fontWeight: "400",
    [theme.breakpoints.down(992)]: {
      marginTop: "4px",
    },
  },
}));
